.rightbarFriend {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    transition: .5s;
    padding: 6px;
    cursor: pointer;
  }
  .rightbarFriend:hover{
    background: rgba(138, 43, 226, 0.6);
    color: #fff;
    border-radius: 7px;
  }
 

  
  .rightbarProfileImgContainer {
    margin-right: 10px;
    position: relative;
  }
  
  .rightbarProfileImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .rightbarOnline{
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background-color: limegreen;
      position: absolute;
      top: -2px;
      right: 0;
      border: 2px solid white;
  }
  
  .rightbarUsername{
      font-weight: 500;
  }